html, body {
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  margin: 0;
  color: rgba(0, 0, 0, 0.8);
  font-weight: 400;
  padding: 20px;
  font-size: 1.6rem;
  font-family: -apple-system,
    BlinkMacSystemFont,
    Lato,
    Droid Sans,
    Helvetica Neue,
    sans-serif;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}

button {
  cursor: pointer;
}

hr {
  margin: 40px 0;
  border: none;
  border-top: 1px solid rgba(0,0,0,0.2);
  background: none;
}



